import {
    audioButtonName,
    videoButtonName
} from './common';
import {
    addContextmenuItem,
    addMenubarItem,
    addToolbarButtons,
    addQuickbarsToolbarItem,
} from 'editor_tiny/utils';

export const configure = (instanceConfig) => {
    const quickInsert = addQuickbarsToolbarItem(
        // Remove the quickimage entirely.
        instanceConfig.quickbars_insert_toolbar?.replace(/quickimage ?/, ''),
        audioButtonName
    );

    // Update the instance configuration to add the Media menu option to the menus and toolbars and upload_handler.
    return {
        contextmenu: addContextmenuItem(instanceConfig.contextmenu, audioButtonName, videoButtonName),
        toolbar: addToolbarButtons(instanceConfig.toolbar, 'content', [audioButtonName, videoButtonName]),
        menu: addMenubarItem(instanceConfig.menu, 'insert', audioButtonName),

        // eslint-disable-next-line camelcase
        quickbars_insert_toolbar: quickInsert,
    };
};
